import React from 'react';

function Card({ id, name, email }) {
	return (
		<div className='tc bg-light-green dib br3 pa3 ma2 graw bw2 shadow-5'>	
			<img src={`https://robohash.org/${name}?200*200`} alt='photo' />
			<div>
				<h2>{name}</h2>
				<p>{email}</p>
			</div>
		</div>
	);
}

export default Card;